export interface Track {
  id: string;
  name: string;
  description: string;
  sport: 'cycling' | 'running' | 'hiking';
  distance: string;
  distanceValue: number;
  difficulty: 'easy' | 'moderate' | 'hard';
  location: string;
  locationRegion: string;
  duration: string;
  elevation: string;
  previewImage: string;
  gpxFile: string;
  mapUrl: string;
  tags: string[];
  createdAt: string;
}

